*, *::before, *::after {
  box-sizing: border-box;
}

*:not(dialog) {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  --cream: rgb(241, 234, 223);
  --pink: #f3d2ca;
  --lime: #ceee54;
  --purple: #530021;
  --grey: #734246;
  --red: #ff8181;
  --invalid-red: #e70049;

  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
  display: grid;
  place-items: center;
  background-color: var(--cream);
  border-radius: 2rem;
  font-family: 'Source Sans 3', 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
  background-color: var(--pink);
  background-color: var(--cream);
  outline-color: var(--purple);
  accent-color: var(--purple);
  border: 2px solid var(--pink);
  border-radius: 0.2rem;
  padding: 0.5rem;
}

input::placeholder {
  color: var(--grey)
}

textarea {
  resize: none;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
  font-family: 'Corben', 'Times New Roman', Times, serif;
  color: var(--purple);
}

.app-container {
  display: grid;
  grid-template-columns: 240px 1fr;
  width: clamp(600px, 90%, 1200px);
  min-height: 100vh;
  border-right: 2px solid var(--pink);
}

nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 2rem 1rem;
  background-color: var(--pink);
}

main > div {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
}

dialog {
  background-color: var(--cream);
  padding: 1.5rem;
  border: 2px solid var(--pink);
  border-radius: 0.5rem;
}

.form-dialog form, .confirmation-dialog {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-field label {
  font-weight: bold;
}

.form-header {
  display: flex;
  flex-direction: row;
  gap: 1.5rem;
  font-size: 2rem;
}

.form-header > input[type="checkbox"] {
  width: clamp(10px, 3rem, 60px);
}

.form-header > input[name="title"] {
  flex-grow: 1;
}

#todo-form-view > form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

input#todo-due-date,
select#todo-priority {
  min-width: 15rem;
  max-width: max-content;
}

.button-container {
  align-self: flex-end;
}

#project-list-container {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
}

.project-item {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.project-item:hover {
  background-color: rgb(from var(--cream) r g b / 0.5);
}

.project-item.selected {
  background-color: var(--cream);
  color: var(--purple);
}

.project-item.selected > p:first-child {
  font-weight: bold;
}

.more-icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: transparent
}

.todo-item:hover .more-icon,
.project-item:hover .more-icon {
  fill: var(--purple);
}

.dropdown-container {
  position: relative;
  color: black;
}

.dropdown-menu {
  position: absolute;
  z-index: 1;
  width: clamp(150px, 12rem, 250px);
  padding: 0.3rem;
  border-radius: 0.3rem;
  background-color: var(--cream);
  box-shadow: 0px 0 0.4rem 0.1rem var(--pink);
}

.dropdown-menu > div {
  padding: 0.5rem 1rem;
}

.dropdown-menu > div:hover {
  font-weight: bold;
  border-radius: 0.2rem;
  background-color: var(--pink);
}

.todo-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  cursor: pointer;
}

.todo-item:hover {
  background-color: var(--pink);
}

.todo-item-details {
  width: 100%;
}

.todo-item-title {
  font-size: 1.15rem;
}

.overdue .todo-item-date {
  color: var(--invalid-red);
}

.todo-item-details.completed p {
  text-decoration: line-through;
  color: var(--grey);
}

button {
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 2rem;
  font-family: 'Corben', 'Times New Roman', Times, serif;
  background-color: var(--lime);
  color: var(--purple);
  cursor: pointer;
}

#add-todo-btn {
  align-self: flex-start;
}

.delete-btn {
  background-color: var(--red);
}

.hide {
  display: none;
